import React, { Component } from "react";
import { Switch, Route, Redirect, withRouter } from "react-router-dom";
import Home from "./Home";
import Header from "./Header";
import Catalog from "./Catalog";
import { Flowers, Veggies, Herbs, Trees } from "./PlantCategories";
import ShoppingBasket from "./ShoppingBasket";
import Social from "./Social";
import Footer from "./Footer";
import About from "./About";
import Contact from "./Contact";
import Covid19Announcement from "./COVID-19";
import { baseUrl } from "../shared/data/baseUrl";

class Main extends Component {
  render() {
    const HomePage = () => {
      return <Home />;
    };

    return (
      <div>
        <Header />
        <Social />
        {/* Subpages */}
        <Switch>
          <Route path={baseUrl + "home"} component={HomePage} />
          <Route exact path={baseUrl + "about_us"} component={About} />
          <Route exact path={baseUrl + "contact_us"} component={Contact} />
          <Route exact path={baseUrl + "catalog"} component={Catalog} />

          {/* Separate Plant Categories */}
          <Route exact path={baseUrl + "flowers"} component={Flowers} />
          <Route exact path={baseUrl + "veggies"} component={Veggies} />
          <Route exact path={baseUrl + "herbs"} component={Herbs} />
          <Route exact path={baseUrl + "trees"} component={Trees} />

          <Route
            exact
            path={baseUrl + "shopping_basket"}
            component={ShoppingBasket}
          />
          <Route
            exact
            path={baseUrl + "covid-19"}
            component={Covid19Announcement}
          />
          <Redirect to={baseUrl} />
        </Switch>

        <Footer />
      </div>
    );
  } // End of render()
} // End of the Main Component

export default withRouter(Main);
